import React from 'react';
import Board from './components/Board';
import Confetti from 'react-confetti';

function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
}

class Game extends React.Component {
  constructor() {
    super();
    this.state = {
      history: [{
        squares: Array(9).fill(null)
      }],
      xIsNext: true,
      stepNumber: 0,
      windowHeight: undefined,
      windowWidth: undefined
    };
  }

  handleResize = () => this.setState({
    windowHeight: window.innerHeight,
    windowWidth: window.innerWidth
  });

  componentDidMount() {
    this.handleResize();
    window.addEventListener('resize', this.handleResize)
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize)
  }

  jumpTo(step) {
    if (step === 0) {
      this.setState({
        history: [{
          squares: Array(9).fill(null)
        }],
        stepNumber: step,
        xIsNext: (step % 2) ? false : true,
      });
    } else {
      this.setState({
        stepNumber: step,
        xIsNext: (step % 2) ? false : true,
      });
    }
  }
  handleClick(i) {
    const history = this.state.history.slice(0, this.state.stepNumber + 1)
    const current = history[history.length - 1];
    const squares = current.squares.slice();
    if (calculateWinner(squares) || squares[i]) {
      return;
    }
    squares[i] = this.state.xIsNext ? '❌' : '⭕️';
    this.setState({
      history: history.concat([{
        squares: squares
      }]),
      xIsNext: !this.state.xIsNext,
      stepNumber: history.length,
    });
  }
  render() {
    const history = this.state.history;
    const current = history[this.state.stepNumber];
    const winner = calculateWinner(current.squares);

    let status;
    if (winner) {
      status = 'Winner: ' + winner;
    } else {
      status = 'Next player: ' + (this.state.xIsNext ? '❌' : '⭕️');
    }

    const moves = history.map((step, move) => {
      console.log(move);
      const player = move % 2 ? 'Player One Move' : 'Player Two Move';
      const desc = move ? `${move} - ${player}` : 'Game start';

      return (
        <li key={move}>
          <a href="#" onClick={() => this.jumpTo(move)}>{desc} {move} </a>
        </li>
      );
    });

    return (
      <div className="game">
        {winner && <Confetti width={this.state.windowWidth} height={this.state.windowHeight} numberOfPieces={'1110'} />}
        <div className="game-board">
          <Board squares={current.squares} onClick={(i) => this.handleClick(i)} />
        </div>
        <div className="game-info">
          <div>{status}</div>
          <h2> MOVES: </h2>
          <ol>{moves}</ol>

        </div>
      </div>
    );
  }
}
export default Game;